.updatePassword-form-container {
  width: 100vw;
  max-width: 100%;
  box-sizing: border-box;
  height: 95vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(241, 241, 149);
}
.updatePassword-form-container > form {
  box-sizing: border-box;
  margin: 1vmax auto;
  width: 35%;
  height: 55vh;
  padding: 3vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  border-radius: 10px;
  background-color: white;
}
.updatePassword-form-container > form > a {
  color: rgb(76, 76, 216);
  cursor: pointer;
  margin-left: auto;
  text-decoration: none;
  margin-right: 10px;
}
.updatePassword-form-container > form > a:hover {
  color: blue;
}
.updatePassword-form-container > form > Button {
  background-color: rgb(5, 135, 241);
  width: 100%;
  margin: 10px 0px;
  transition: 0.5s ease all;
  color: white;
  margin-top: 15px;
}
.updatePassword-form-container > form > Button:hover {
  background-color: rgb(36, 95, 143);
}
.updatePassword-form-container > form > .updatePassword-input {
  width: 100%;
  height: 40px;
  height: 13%;
  padding: 0px 20px;
  margin: 0.5vmax 0px;
  outline: none;
  border-radius: 50px;
  border: none;
  border: 1px solid rgb(5, 135, 241);
}
