.file-input::-webkit-file-upload-button {
  background-color: rgb(38, 63, 173);
  color: white;
  width: 100%;
  border-radius: 20px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1vmax;
  overflow: visible;
  font: 100 1.2rem "Roboto", sans-serif;
}

@media screen and (max-width: 600px) {
  .updatePassword-form-container > form {
    padding: 1vmax;
    width: 100%;
    margin: 0px 10px;
  }
}
